import axios from "axios";
import React, { Component, Fragment } from "react";
import { BiLoaderCircle, BiWorld } from "react-icons/bi";
import { BsClipboardData } from "react-icons/bs";
import { CgChevronDoubleRight } from "react-icons/cg";
import { FaUserTag } from "react-icons/fa";
import { GoFilePdf } from "react-icons/go";
import { MdAdsClick, MdSelectAll } from "react-icons/md";
import { connect } from "react-redux";
import {
  API_GetPlacementBySchoolUpdated,
  Auth,
  Basic,
  FC_GetAcademics,
  Placement,
  FC_GetReportByDistrict,
  FC_GetDistrictList,
  DistrictList,
  FC_GetReportBySchoolMonthly,
} from "../../actions";
import Container from "../../components/ContainerComponents/Container";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { APP_URL } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { ReportAcademicYearType } from "../../types/PlacementReportTypes";
import { MONTHS_LIST } from "../../utils/functions";
import { Annex1 } from "./Annex1/Annex1";
import {
  TEACHING_LEVEL,
  PlacementReportTypes,
  SchoolPlacement,
  UserInfoGet,
} from "./SchoolPlacement/SchoolPlacement";
import { SelectDistrict } from "./SchoolPlacement/SelectDistrict";
import SelectRole from "./SchoolPlacement/SelectRole";
import SelectSchool from "./SchoolPlacement/SelectSchool";

interface PlacementBooksProps {
  auth: Auth;
  basic: Basic;
  placement: Placement;
  FC_GetAcademics: (
    callBack: (
      status: boolean,
      data: ReportAcademicYearType[] | null,
      msg: string
    ) => void
  ) => void;
}
export interface Annex1ReportItem {
  qualification_id: string;
  qualification_name: string;
  teaching_level: TEACHING_LEVEL;
  teaching_level_id: string;
  total: string;
}
export interface Annex1ReportInterface {
  serving_teachers: Annex1ReportItem[];
  new_teachers: Annex1ReportItem[];
  vacants: Annex1ReportItem[];
  users?: {
    dde_id: string;
    dde_name: string;
    mayor_id: string;
    mayor_name: string;
  };
}

interface PlacementBooksState {
  loading: boolean;
  book_type: "placement_book" | "annex_1" | "annex_2";
  error: {
    element: string;
    msg: string;
  } | null;
  selected_academic: string | null;
  selected_month: string;
  placement_report: API_GetPlacementBySchoolUpdated[] | null;
  selected_school_code: string;
  selected_district_code: string;
  open_select_school: boolean;
  districtList: DistrictList[] | null;
  open_select_district: boolean;
  open_select_role: boolean;
  selected_role: {
    role_id: string;
    role_name: string;
  }[];
  show_district_all_data: boolean;
  reportType: {
    key: TEACHING_LEVEL;
    value: string;
  };
  annex1Report: Annex1ReportInterface | null;
}

