const TodayDate = new Date();

export class _PlacementBooks extends Component<
  PlacementBooksProps,
  PlacementBooksState
> {
  constructor(props: PlacementBooksProps) {
    super(props);

    this.state = {
      loading: false,
      // book_type:
      // this.props.auth.role?.role_id !== "2" ? "annex_1" : "placement_book",
      book_type: "placement_book",
      error: null,
      selected_academic:
        this.props.basic.academic_calender === null
          ? null
          : this.props.basic.academic_calender.academic_year_id,
      selected_month: MONTHS_LIST[TodayDate.getMonth()],
      // selected_month: "May",
      placement_report: null,
      selected_district_code:
        this.props.auth.district === null
          ? ""
          : this.props.auth.role?.role_id === "3"
          ? this.props.auth.district.district_code
          : "",
      selected_school_code:
        this.props.auth.role?.role_id === "2" && this.props.auth.school !== null
          ? this.props.auth.school.school_code
          : "",
      open_select_school: false,
      districtList: null,
      open_select_district: false,
      open_select_role: false,
      selected_role: [],
      show_district_all_data: false,
      reportType: {
        key: TEACHING_LEVEL.primary,
        value: "PRIMARY",
      },
      annex1Report: null,
    };
  }

  setLoading = (status: boolean) => this.setState({ loading: status });

  getSelectedReport = (
    selected_month: string
  ): null | API_GetPlacementBySchoolUpdated => {
    let selected = this.state.placement_report?.find(
      (itm) =>
        itm.month === selected_month &&
        (this.state.selected_school_code === null ||
          this.state.selected_school_code === itm.school_code ||
          this.props.auth.role?.role_id === "2")
    );
    if (selected !== null && selected !== undefined) {
      return selected;
    } else {
      return null;
    }
  };

  getSelectedReportDistrict = (
    selected_district_code: string | null
  ): null | API_GetPlacementBySchoolUpdated[] => {
    let selected_check = this.state.placement_report?.filter(
      (itm) =>
        itm.district_code === selected_district_code ||
        selected_district_code === null
    );
    if (selected_check !== null && selected_check !== undefined) {
      return selected_check;
    } else {
      return null;
    }
  };

  FC_getSchoolReport = (selected_moth: string) => {
    if (
      this.props.auth.role !== null &&
      this.props.auth.school !== null &&
      this.props.auth.role.role_id === "2" &&
      this.state.selected_academic !== null
    ) {
      //Head Teacher
      // Load placement list of selected school
      this.setLoading(true);
      this.setState({ placement_report: null });
      FC_GetReportBySchoolMonthly(
        this.props.auth.school?.school_code,
        this.state.selected_academic,
        selected_moth,
        (
          status: boolean,
          msg: string,
          data: API_GetPlacementBySchoolUpdated[] | null
        ) => {
          if (status === false) {
            this.setState({
              placement_report: data === null ? [] : data,
              selected_school_code:
                this.state.selected_district_code !== "" &&
                data !== null &&
                data.length > 0
                  ? data[0].school_code
                  : "",
            });
            this.setLoading(false);
          }
        }
      );
    }
  };

  FC_GetDistrictReport = (
    district_code: string | undefined,
    selected_academic: string | null,
    selected_month: string
  ) => {
    if (district_code !== undefined && selected_academic !== null) {
      this.setLoading(true);
      this.setState({ placement_report: null });
      FC_GetReportByDistrict(
        district_code,
        selected_academic,
        selected_month,
        (
          status: boolean,
          msg: string,
          data: API_GetPlacementBySchoolUpdated[] | null
        ) => {
          this.setState({ placement_report: data });
          this.setLoading(false);
        }
      );
    } else {
      alert("Not found!");
    }
  };

  getSelectedSchoolReport = (
    data: API_GetPlacementBySchoolUpdated[] | null
  ): API_GetPlacementBySchoolUpdated | null => {
    const test =
      this.props.auth.district !== null &&
      data?.find((itm) => itm.school_code === this.state.selected_school_code);
    if (test !== undefined && test !== false) {
      return test;
    } else {
      return null;
    }
  };

  getSchoolDetailsByCode = (
    data: API_GetPlacementBySchoolUpdated[] | null,
    school_district_code: string
  ): API_GetPlacementBySchoolUpdated | null => {
    const test = data?.find(
      (itm) =>
        itm.school_code === school_district_code ||
        itm.district_code === school_district_code
    );
    if (test !== undefined) {
      return test;
    } else {
      return null;
    }
  };

  FilterByRole = (dataSet: API_GetPlacementBySchoolUpdated[]) => {
    const res: API_GetPlacementBySchoolUpdated[] = [];
    for (const data of dataSet) {
      let new_list: UserInfoGet[] = [];
      for (const sub of JSON.parse(data.list) as UserInfoGet[]) {
        if (
          this.state.selected_role.find((t) => t.role_id === sub.role_id) !==
          undefined
        ) {
          new_list.push(sub);
        }
      }
      res.push({
        list: JSON.stringify(new_list),
        placement_list_approval_id: data.placement_list_approval_id,
        school_code: data.school_code,
        district_code: data.district_code,
        academic_year_id: data.academic_year_id,
        head_teacher_id: data.head_teacher_id,
        dde_id: data.dde_id,
        dde_approval_status: data.dde_approval_status,
        approved_date: data.approved_date,
        month: data.month,
        created_by: data.created_by,
        created_at: data.created_at,
        updated_by: data.updated_by,
        updated_at: data.updated_at,
        school_name: data.school_name,
        district_name: data.district_name,
        ht_comment: data.ht_comment,
        dde_comment: data.dde_comment,
        users: data.users,
      });
    }
    return res;
  };

  // Get Annex report

  GetAnnex1ReportByDistrict = async (selected_district_code: string) => {
    if (selected_district_code !== "") {
      this.setLoading(true);
      try {
        const res = await axios.get<Annex1ReportInterface>(
          `${APP_URL}/report/country/level/index/${selected_district_code}`
        );
        if (res) {
          this.setState({ annex1Report: res.data, loading: false });
        }
      } catch (error: any) {
        console.log("Err: ", { ...error });
        this.setState({ loading: false });
      }
    }
  };

  GetAnnex1Report = (
    book_type: "placement_book" | "annex_1" | "annex_2",
    selected_district_code: string
  ) => {
    if (
      selected_district_code !== "" &&
      this.state.annex1Report === null &&
      book_type === "annex_1"
    ) {
      this.GetAnnex1ReportByDistrict(selected_district_code);
    }
    this.setLoading(false);
  };

  LoadPlacementReport = (
    book_type: "placement_book" | "annex_1" | "annex_2",
    selected_district_code: string
  ) => {
    if (
      this.state.placement_report === null &&
      this.props.auth.role?.role_id === "2" &&
      this.state.selected_month !== "" &&
      book_type === "placement_book"
    ) {
      // Get school report
      this.FC_getSchoolReport(this.state.selected_month);
    }
    if (
      this.props.auth.role?.role_id !== "2" &&
      book_type === "placement_book"
    ) {
      //DDE Report
      this.FC_GetDistrictReport(
        selected_district_code,
        this.state.selected_academic,
        this.state.selected_month
      );
    }
    this.setLoading(false);
  };

  componentDidMount = () => {
    // printTestingReport();
    if (this.state.districtList === null) {
      this.setLoading(true);
      FC_GetDistrictList(
        (status: boolean, data: DistrictList[], msg: string) => {
          this.setState({ districtList: data });
          this.setLoading(false);
        }
      );
    }

    if (this.props.placement.academics === null) {
      this.setLoading(true);
      this.props.FC_GetAcademics(
        (
          status: boolean,
          data: ReportAcademicYearType[] | null,
          msg: string
        ) => {
          if (status === false) {
            if (this.state.selected_academic === null) {
              data !== null &&
                this.setState({
                  selected_academic:
                    data.length > 0
                      ? data[data.length - 1].academic_year_id.toString()
                      : null,
                });
            }
            if (
              this.state.selected_district_code === "" &&
              this.props.auth.role?.role_id.toString() === "4"
            ) {
              this.setState({ open_select_district: true });
            }
            // Get annex report
            this.GetAnnex1Report(
              this.state.book_type,
              this.state.selected_district_code
            );

            // Load placement report
            this.LoadPlacementReport(
              this.state.book_type,
              this.state.selected_district_code
            );

            this.setLoading(false);
          }
        }
      );
      if (this.props.basic.academic_calender !== null) {
        this.setState({
          selected_academic:
            this.props.basic.academic_calender.academic_year_id,
        });
      }
    }
    if (
      this.state.placement_report === null &&
      this.props.auth.role?.role_id === "2" &&
      this.state.selected_month !== "" &&
      this.state.book_type === "placement_book"
    ) {
      // Get school report
      this.FC_getSchoolReport(this.state.selected_month);
    }
  };
  getRolesList = (
    placement_report: API_GetPlacementBySchoolUpdated[] | null
  ): { role_id: string; role_name: string }[] => {
    let roles: { role_id: string; role_name: string }[] = [];
    if (placement_report !== null) {
      for (const role of placement_report) {
        for (const item of JSON.parse(role.list) as UserInfoGet[]) {
          if (roles.find((itm) => itm.role_id === item.role_id) === undefined) {
            roles.push({ role_id: item.role_id, role_name: item.role });
          }
        }
      }
    }
    return roles;
  };
  render() {
    if (
      this.state.loading === true ||
      (this.state.placement_report === null &&
        this.state.book_type === "placement_book") ||
      (this.state.book_type === "annex_1" &&
        this.state.annex1Report === null &&
        this.state.selected_district_code !== "" &&
        this.props.auth.role?.role_id.toString() !== "4")
    ) {
      return (
        <DashboardContainer className="bg-white p-3 flex flex-col items-center justify-center gap-2 pt-6">
          {this.state.book_type === "placement_book" ? (
            <>
              <div className="text-2xl font-extrabold mb-3 border-b pb-3 w-full text-center">
                Generating Placement Report
              </div>
              <div className="flex flex-row items-center justify-center gap-2 bg-gray-100 rounded-md px-5 py-5">
                <div className="mr-4">
                  <BsClipboardData className="text-8xl animate__animated animate__infinite text-gray-400" />
                </div>
                <div>
                  <CgChevronDoubleRight className="text-8xl animate__animated animate__fadeInLeft animate__infinite text-gray-500" />
                </div>
                <div>
                  <GoFilePdf className="text-8xl animate__animated  animate__infinite text-accent-900" />
                </div>
              </div>
            </>
          ) : (
            <Loading />
          )}
          <div className="text-lg font-light flex flex-row items-center gap-2">
            <div>
              <BiLoaderCircle className="text-2xl animate-spin text-accent-900" />
            </div>
            {this.state.book_type === "placement_book" ? (
              <span>Loading data and generating PDF, please wait...</span>
            ) : (
              <span>Loading data, please wait...</span>
            )}
          </div>
        </DashboardContainer>
      );
    }
    return (
      <Fragment>
        <div>
          <Container
            title={"Placement report"}
            subTitle={""}
            className="overflow-y-auto"
          >
            <div className="flex flex-row items-center gap-3 justify-between">
              {this.props.auth.role?.role_id !== "2" ? (
                <div className="flex flex-row items-center w-full gap-2 mt-3">
                  <div className="flex flex-row items-center w-full bg-white">
                    <div
                      className={`px-3 py-1 text-center border-b-4 cursor-pointer hover:bg-primary-100 hover:border-primary-800 w-max ${
                        this.state.book_type === "placement_book"
                          ? "border-primary-800 text-primary-800 text-lg bg-primary-100"
                          : "border-gray-100 text-black text-lg bg-gray-100"
                      }`}
                      onClick={() => {
                        this.setState({ book_type: "placement_book" });
                        this.LoadPlacementReport(
                          "placement_book",
                          this.state.selected_district_code
                        );
                      }}
                    >
                      <span className="truncate">School placement report</span>
                    </div>
                    <div
                      className={`px-3 py-1 text-center border-b-4 cursor-pointer hover:bg-primary-100 hover:border-primary-800 w-40 ${
                        this.state.book_type === "annex_1"
                          ? "border-primary-800 text-primary-800 text-lg bg-primary-100"
                          : "border-gray-100 text-black text-lg bg-gray-100"
                      }`}
                      onClick={() => {
                        this.setState({ book_type: "annex_1" });
                        this.GetAnnex1Report(
                          "annex_1",
                          this.state.selected_district_code
                        );
                      }}
                    >
                      <span>Annex 1 report</span>
                    </div>
                    {false && (
                      <div
                        className={`px-3 py-1 text-center border-b-4 cursor-pointer hover:bg-primary-100 hover:border-primary-800 w-40 ${
                          this.state.book_type === "annex_2"
                            ? "border-primary-800 text-primary-800 text-lg bg-primary-100"
                            : "border-gray-100 text-black text-lg bg-gray-100"
                        }`}
                        onClick={() => {
                          this.setState({ book_type: "annex_2" });
                        }}
                      >
                        <span>Annex 2 report</span>
                      </div>
                    )}
                  </div>
                </div>
              ) : (
                <div className="flex flex-col mt-2">
                  <span className="text-gray-600 text-sm">Selected school</span>
                  <div className="font-extrabold text-lg">
                    {this.props.auth.school?.school_name !== undefined &&
                      `(${this.props.auth.school?.school_code})`}{" "}
                    {this.props.auth.school?.school_name}
                  </div>
                </div>
              )}
              {/* Calendar and months */}
              {this.props.placement.academics !== null && (
                <div className="flex flex-row items-center justify-end gap-3">
                  <div className="flex flex-col">
                    <span>Academic year</span>
                    <select
                      value={
                        this.state.selected_academic === null
                          ? ""
                          : this.state.selected_academic
                      }
                      onChange={(e) => {
                        if (e.target.value !== "") {
                          this.setState({ selected_academic: e.target.value });
                          this.props.auth.role?.role_id === "2" &&
                            this.FC_getSchoolReport(this.state.selected_month);
                          this.props.auth.role?.role_id !== "2" &&
                            this.FC_GetDistrictReport(
                              this.state.selected_district_code,
                              this.state.selected_academic,
                              this.state.selected_month
                            );
                        }
                      }}
                      className="border border-gray-400 rounded px-3 py-2"
                    >
                      <option value="">Choose academic</option>
                      {this.props.placement.academics.map((item, i) => (
                        <option key={i + 1} value={item.academic_year_id}>
                          {item.academic_year_name}
                        </option>
                      ))}
                    </select>
                  </div>

                  {this.state.book_type === "placement_book" && (
                    <div className="flex flex-col">
                      <div className="flex flex-row items-center gap-3">
                        {((this.state.placement_report !== null &&
                          this.state.placement_report.length === 0) ||
                          this.state.placement_report === null) && (
                          <div>
                            <MdAdsClick className="text-3xl text-accent-800 animate__animated animate__heartBeat animate__infinite -mb-10 -ml-2" />
                          </div>
                        )}
                        <span className={`truncate`}>Selected month</span>
                      </div>

                      <select
                        value={this.state.selected_month}
                        onChange={(e) => {
                          this.setState({
                            placement_report: null,
                            selected_month: e.target.value,
                          });
                          this.props.auth.role?.role_id === "2" &&
                            this.FC_getSchoolReport(e.target.value);
                          this.props.auth.role?.role_id !== "2" &&
                            this.FC_GetDistrictReport(
                              this.state.selected_district_code,
                              this.state.selected_academic,
                              e.target.value
                            );
                        }}
                        className={`border ${
                          (this.state.placement_report !== null &&
                            this.state.placement_report.length === 0) ||
                          this.state.placement_report === null
                            ? "border-accent-800 text-accent-900"
                            : "border-gray-400"
                        } rounded px-3 py-2`}
                      >
                        <option value="">Choose month</option>
                        {MONTHS_LIST.map((item, i) => (
                          <option key={i + 1} value={item}>
                            {item}
                          </option>
                        ))}
                      </select>
                    </div>
                  )}
                </div>
              )}
            </div>
            {/* Descriptions */}
            <div className="my-3 flex flex-row items-center gap-3 justify-between w-full">
              <div className="-mb-5 w-full">
                <div className="flex flex-row items-center gap-3">
                  {this.props.auth.role?.role_id !== "2" &&
                    this.props.auth.role?.role_id !== "3" &&
                    this.state.districtList !== null && (
                      <div className="flex flex-col">
                        <div
                          onClick={() =>
                            this.setState({ open_select_district: true })
                          }
                          className="flex flex-row mb-2 items-center gap-2 bg-accent-400 text-accent-900 hover:bg-accent-800 cursor-pointer hover:text-black px-2 py-2 rounded truncate w-max"
                        >
                          <div>
                            <MdSelectAll className="text-2xl" />
                          </div>
                          <span>
                            {this.state.selected_district_code !== "" &&
                            this.state.districtList !== null
                              ? this.state.districtList.find(
                                  (itm) =>
                                    itm.district_code ===
                                    this.state.selected_district_code
                                )?.district_name
                              : "Choose district"}
                          </span>
                        </div>
                      </div>
                    )}
                  {/* {this.props.auth.role?.role_id !== "2" &&
                    this.state.selected_district_code !== "" && (
                      <div
                        onClick={() =>
                          this.setState({
                            show_district_all_data:
                              !this.state.show_district_all_data,
                          })
                        }
                        className="flex flex-row items-center bg-primary-100 rounded px-2 cursor-pointer py-2 gap-2 -mt-2"
                      >
                        <div>
                          {this.state.show_district_all_data === false ? (
                            <MdCheckBoxOutlineBlank className="text-2xl" />
                          ) : (
                            <ImCheckboxChecked className="text-2xl" />
                          )}
                        </div>
                        <div>District level</div>
                      </div>
                    )} */}
                  {this.state.book_type === "placement_book" &&
                    this.props.auth.role?.role_id !== "2" &&
                    this.getSelectedReportDistrict(
                      this.state.selected_district_code
                    ) !== null &&
                    this.state.show_district_all_data === false &&
                    this.getSelectedReportDistrict(
                      this.state.selected_district_code
                    ) !== null &&
                    this.getSelectedReportDistrict(
                      this.state.selected_district_code
                    )?.length !== 0 && (
                      <div className="flex flex-col">
                        <div
                          onClick={() =>
                            this.setState({
                              open_select_school: true,
                              selected_school_code: "",
                            })
                          }
                          className={`flex flex-row mb-2 items-center gap-2 ${
                            this.state.selected_school_code === ""
                              ? "bg-accent-800 text-black"
                              : "bg-accent-400 text-accent-900"
                          } hover:bg-accent-800 cursor-pointer hover:text-black px-2 py-2 rounded truncate w-max`}
                        >
                          <div>
                            {this.state.selected_school_code === "" ? (
                              <MdAdsClick className="text-2xl animate__animated animate__heartBeat animate__infinite" />
                            ) : (
                              <MdSelectAll className={`text-2xl`} />
                            )}
                          </div>
                          <span>
                            {this.state.selected_district_code !== "" &&
                            this.getSchoolDetailsByCode(
                              this.getSelectedReportDistrict(
                                this.state.selected_district_code
                              ),
                              this.state.selected_school_code
                            ) !== null
                              ? this.state.selected_district_code !== "" &&
                                this.getSchoolDetailsByCode(
                                  this.getSelectedReportDistrict(
                                    this.state.selected_district_code
                                  ),
                                  this.state.selected_school_code
                                )?.school_name
                              : "Choose school"}
                          </span>
                        </div>
                      </div>
                    )}
                  {this.props.auth.role?.role_id !== "2" &&
                    this.state.selected_district_code !== "" &&
                    this.state.selected_school_code !== "" && (
                      <div
                        onClick={() =>
                          this.setState({ selected_school_code: "" })
                        }
                        className="-mt-2 px-3 py-2 font-bold rounded cursor-pointer bg-accent-400 hover:text-accent-900 w-max"
                      >
                        View by all schools
                      </div>
                    )}
                  {this.state.book_type === "placement_book" &&
                    this.props.auth.role?.role_id !== "2" &&
                    this.state.selected_district_code !== "" &&
                    this.state.selected_school_code === "" && (
                      <div>
                        <select
                          value={this.state.reportType.key}
                          onChange={(e) => {
                            if (e.target.value !== "") {
                              this.setState({
                                loading: true,
                                selected_school_code: "",
                              });
                              const typ = PlacementReportTypes.find(
                                (itm) => itm.key === e.target.value
                              );
                              if (typ !== undefined) {
                                this.setState({ reportType: typ });
                              }
                            }
                            this.setLoading(false);
                          }}
                          className="px-3 py-2 rounded border border-gray-500 font-bold -mt-2"
                        >
                          <option value="">Choose report type</option>
                          {PlacementReportTypes.map((r_type, r) => (
                            <option key={r + 1} value={r_type.key}>
                              {r_type.value}
                            </option>
                          ))}
                        </select>
                      </div>
                    )}
                  {this.props.auth.role?.role_id !== "2" &&
                    this.getSelectedReportDistrict(
                      this.state.selected_district_code
                    ) !== null &&
                    this.getSelectedReportDistrict(
                      this.state.selected_district_code
                    )?.length !== 0 &&
                    this.state.show_district_all_data === true && (
                      <div className="flex flex-col">
                        <div
                          onClick={() =>
                            this.setState({ open_select_role: true })
                          }
                          className={`flex flex-row mb-2 items-center gap-2 ${
                            this.state.selected_school_code === ""
                              ? "bg-accent-800 text-black"
                              : "bg-accent-400 text-accent-900"
                          } hover:bg-accent-800 cursor-pointer hover:text-black px-2 py-2 rounded truncate w-max`}
                        >
                          <div>
                            {this.state.selected_school_code === "" ? (
                              <MdAdsClick className="text-2xl animate__animated animate__heartBeat animate__infinite" />
                            ) : (
                              <FaUserTag className={`text-2xl`} />
                            )}
                          </div>
                          <span>{"Filter by role"}</span>
                        </div>
                      </div>
                    )}
                </div>
                {this.state.book_type === "placement_book" &&
                  (this.state.placement_report === null ? (
                    <div className="bg-white w-full p-5 rounded font-bold text-xl -mx-3 text-accent-900">
                      Please choose valid month
                    </div>
                  ) : this.state.placement_report.length === 0 ? (
                    <div className="bg-white w-full p-5 rounded font-bold text-xl -mx-3">
                      No result found for selected month (
                      {this.state.selected_month})
                    </div>
                  ) : (
                    <div className="mb-5">
                      Placement list report of teachers from schools
                    </div>
                  ))}
                {this.state.book_type === "annex_1" && (
                  <div>
                    General district level summary of teacher placement report
                  </div>
                )}
                {this.state.book_type === "annex_2" && (
                  <div>
                    List of new teachers placed within the selected academic
                    year
                    {/* <div
                      onClick={() => printTestingReport()}
                      className="bg-accent-400 font-bold text-base px-3 py-2 rounded cursor-pointer w-max my-3 mb-6"
                    >
                      Download report
                    </div> */}
                  </div>
                )}
              </div>
              <div>
                {this.state.book_type === "placement_book" &&
                  this.state.placement_report !== null &&
                  this.props.auth.role?.role_id !== "2" && (
                    <div
                      className={`flex flex-row items-center gap-2 ${
                        this.state.placement_report.length === 0
                          ? "bg-yellow-100 text-accent-900"
                          : "bg-primary-100 text-primary-800"
                      } rounded-md px-2 py-1`}
                    >
                      <span className="">Results(schools): </span>
                      <span className="font-extrabold">
                        {this.state.placement_report.length}
                      </span>
                    </div>
                  )}
              </div>
            </div>
            {/* Report here for Head Teacher*/}
            {this.state.book_type === "placement_book" &&
              (this.props.auth.role?.role_id === "2" ||
                this.state.selected_school_code !== "") &&
              this.state.book_type === "placement_book" &&
              this.state.placement_report !== null &&
              this.getSelectedReport(this.state.selected_month) !== null && (
                <SchoolPlacement
                  data={this.getSelectedReport(this.state.selected_month)!}
                  basic={this.props.basic}
                  selected_academic={this.props.placement.academics?.find(
                    (itm) =>
                      itm.academic_year_id.toString() ===
                      this.state.selected_academic
                  )}
                  headTeacherNames={`${
                    this.getSelectedReport(
                      this.state.selected_month
                    )!.users?.find((itm) => itm.role_id === "2")?.first_name
                  } ${
                    this.getSelectedReport(
                      this.state.selected_month
                    )!.users?.find((itm) => itm.role_id === "2")?.last_name
                  }`}
                  schoolName={
                    this.getSelectedReport(this.state.selected_month)!
                      .school_name
                  }
                  districtName={
                    this.getSelectedReport(this.state.selected_month)!
                      .district_name === undefined
                      ? ""
                      : this.getSelectedReport(this.state.selected_month)!
                          .district_name!
                  }
                  ddeName={`${
                    this.getSelectedReport(
                      this.state.selected_month
                    )!.users?.find((itm) => itm.role_id === "3")?.first_name
                  } ${
                    this.getSelectedReport(
                      this.state.selected_month
                    )!.users?.find((itm) => itm.role_id === "3")?.last_name
                  }`}
                  mayorName={"................................"}
                  auth={this.props.auth}
                  reportType={this.state.reportType}
                  district_data={this.state.placement_report}
                  selected_school={
                    this.props.auth.role?.role_id === "2" &&
                    this.props.auth.school !== null
                      ? this.props.auth.school.school_code
                      : this.state.selected_school_code
                  }
                  setLoading={this.setLoading}
                />
              )}
            {(this.state.placement_report === null &&
              this.state.book_type === "placement_book") ||
            (this.state.annex1Report === null &&
              this.state.book_type === "annex_1") ? (
              <div className="font-extrabold w-full p-5 text-gray-500 bg-gray-100 rounded-md mt-10 text-2xl">
                Loading, please wait...
              </div>
            ) : (
              this.props.auth.role?.role_id === "2" &&
              this.state.book_type === "placement_book" &&
              this.getSelectedReport(this.state.selected_month) === null && (
                <div className="font-extrabold w-full p-5 text-gray-500 bg-gray-100 rounded-md mt-10 text-2xl">
                  No report available
                </div>
              )
            )}
            {/* -------------------end---------------- */}
            {/* Report for top level */}
            {this.state.book_type === "placement_book" &&
              this.props.auth.role?.role_id !== "2" &&
              this.state.selected_district_code !== "" &&
              this.state.placement_report !== null &&
              this.state.placement_report.length > 0 &&
              this.state.selected_school_code === "" && (
                <SchoolPlacement
                  data={this.getSelectedSchoolReport(
                    this.getSelectedReportDistrict(
                      this.state.selected_district_code
                    )
                  )}
                  basic={this.props.basic}
                  selected_academic={this.props.placement.academics?.find(
                    (itm) =>
                      itm.academic_year_id.toString() ===
                      this.state.selected_academic
                  )}
                  headTeacherNames={""}
                  schoolName={""}
                  districtName={""}
                  ddeName={""}
                  mayorName={"................................"}
                  auth={this.props.auth}
                  reportType={this.state.reportType}
                  district_data={this.state.placement_report}
                  selected_school={
                    this.props.auth.role?.role_id === "2" &&
                    this.props.auth.school !== null
                      ? this.props.auth.school.school_code
                      : this.state.selected_school_code
                  }
                  setLoading={this.setLoading}
                />
              )}
            {/* -------------------END---------------- */}
            {/* Annex report */}
            {this.state.book_type === "annex_1" &&
              this.state.annex1Report !== null && (
                <Annex1
                  auth={this.props.auth}
                  data={this.state.annex1Report}
                  selected_district={this.state.districtList?.find(
                    (itm) =>
                      itm.district_code === this.state.selected_district_code
                  )}
                  qualifications={this.props.basic.qualifications}
                />
              )}
          </Container>
        </div>
        {this.state.open_select_school === true &&
          this.props.auth.role?.role_id !== "2" &&
          this.state.selected_district_code !== "" &&
          // ||
          // (this.state.placement_report !== null &&
          //   this.state.placement_report.length > 0 &&
          //   this.state.selected_school_code === "" &&
          //   this.props.auth.role?.role_id !== "2")
          this.getSelectedReportDistrict(this.state.selected_district_code) !==
            null &&
          this.getSelectedReportDistrict(this.state.selected_district_code)
            ?.length !== 0 && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ open_select_school: false })}
              backDropClose={true}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              padding={{
                title: true,
                body: true,
              }}
              title={
                <div className="text-3xl font-extrabold -mb-3">
                  <div className="flex flex-col">
                    <span>Schools list</span>
                    <div className="px-2 py-1 text-base font-extrabold rounded-md w-max bg-accent-400 border-yellow-400 text-accent-900 flex flex-row items-center gap-2">
                      {this.state.selected_district_code !== null && (
                        <div>
                          <BiWorld className="text-2xl" />
                        </div>
                      )}
                      <span>
                        {this.state.selected_district_code !== null &&
                        this.getSchoolDetailsByCode(
                          this.getSelectedReportDistrict(
                            this.state.selected_district_code
                          ),
                          this.state.selected_district_code
                        ) !== null
                          ? this.state.selected_district_code !== null &&
                            this.getSchoolDetailsByCode(
                              this.getSelectedReportDistrict(
                                this.state.selected_district_code
                              ),
                              this.state.selected_district_code
                            )?.district_name
                          : ""}
                      </span>
                    </div>
                  </div>
                </div>
              }
            >
              <SelectSchool
                getSelectedReportDistrict={this.getSelectedReportDistrict(
                  this.state.selected_district_code
                )}
                select_district={(school_code: string) => {
                  this.setState({
                    selected_school_code: school_code,
                    open_select_school: false,
                  });
                }}
                selected_school_code={this.state.selected_school_code}
              />
            </Modal>
          )}
        {((this.state.districtList !== null &&
          this.state.selected_district_code === "") ||
          this.state.open_select_district === true) &&
          this.props.auth.role?.role_id !== "2" && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ open_select_district: false })}
              backDropClose={true}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              padding={{
                title: true,
                body: true,
              }}
              title={
                <div className="text-3xl font-extrabold">Choose district</div>
              }
            >
              <div className="-mt-5">
                <SelectDistrict
                  districtList={this.state.districtList}
                  selected_district_code={this.state.selected_district_code}
                  onSelect={(item: DistrictList) => {
                    this.setState({
                      selected_district_code: item.district_code,
                      selected_school_code: "",
                      open_select_district: false,
                      placement_report: null,
                    });
                    // this.props.auth.role?.role_id !== "2" &&
                    //   this.FC_GetDistrictReport(
                    //     item.district_code,
                    //     this.state.selected_academic,
                    //     this.state.selected_month
                    //   );
                    this.LoadPlacementReport(
                      this.state.book_type,
                      item.district_code
                    );
                    this.GetAnnex1Report(
                      this.state.book_type,
                      item.district_code
                    );
                  }}
                />
              </div>
            </Modal>
          )}
        {this.state.open_select_role === true &&
          this.state.selected_district_code !== "" && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ open_select_role: false })}
              backDropClose={true}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              padding={{
                title: true,
                body: true,
              }}
              title={
                <div className="text-3xl font-extrabold">
                  Choose teacher role
                </div>
              }
            >
              <SelectRole
                selected_role={this.state.selected_role}
                updateRoles={(
                  data: { role_id: string; role_name: string }[]
                ) => {
                  this.setState({
                    selected_role: data,
                    open_select_role: false,
                  });
                }}
                roles_list={
                  this.state.placement_report === null
                    ? []
                    : this.getRolesList(this.state.placement_report)
                }
              />
            </Modal>
          )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  placement,
}: StoreState): { auth: Auth; basic: Basic; placement: Placement } => {
  return { auth: auth, basic: basic, placement: placement };
};

export const PlacementBooks = connect(mapStateToProps, {
  FC_GetAcademics,
})(_PlacementBooks);
