import React, { Component } from "react";
import { Auth, DistrictList, Qualifications } from "../../../actions";
import { Annex1ReportInterface } from "../PlacementBooks";
import { DataFormat } from "./DataFormat";

interface Annex1Props {
  auth: Auth;
  data: Annex1ReportInterface;
  selected_district: DistrictList | undefined;
  qualifications: Qualifications[];
}
interface Annex1State {
  loading: boolean;
}

export const FindTotalByQualification = (
  data: Annex1ReportInterface,
  qualification_id: string,
  level: "primary" | "pre-primary" | "secondary",
  filterStatus: "Active" | "New" | "Vacant" | "Total" | string
) => {
  let total = 0;
  let search_array =
    filterStatus === "Active"
      ? data.serving_teachers
      : filterStatus === "New"
      ? data.new_teachers
      : filterStatus === "Vacant"
      ? data.vacants
      : data.serving_teachers;
  if (search_array === undefined) {
    search_array = [];
  }
  for (const temp_data of search_array) {
    if (
      temp_data.qualification_id === qualification_id &&
      temp_data.teaching_level.toLocaleLowerCase() === level.toLocaleLowerCase()
    ) {
      total += parseInt(temp_data.total);
    }

    // for (let item of JSON.parse(temp_data.list) as UserInfoGet[]) {
    //   if (
    //     item.qualification === qualification &&
    //     item.teacher_hierarch !== null &&
    //     item.teacher_hierarch.find(
    //       (itm) =>
    //         itm.combination_name.toLocaleLowerCase() ===
    //         level.toLocaleLowerCase()
    //     ) !== undefined &&
    //     (item.status.toLocaleLowerCase() === filterStatus.toLocaleLowerCase() ||
    //       filterStatus.toLocaleLowerCase() === "Total".toLocaleLowerCase())
    //   ) {
    //     total += 1;
    //   }
    // }
  }
  return total;
};

export class Annex1 extends Component<Annex1Props, Annex1State> {
  constructor(props: Annex1Props) {
    super(props);

    this.state = {
      loading: false,
    };
  }
  render() {
    return (
      <div>
        {this.props.selected_district !== undefined && (
          <DataFormat
            auth={this.props.auth}
            data={this.props.data}
            selected_district={this.props.selected_district}
            qualifications={this.props.qualifications}
          />
        )}
      </div>
    );
  }
}
